<template>
  <el-container class="dashboard-container">
    <el-aside :width="isCollapsed ? '80px' : '200px'" class="sidebar" :class="{ collapsed: isCollapsed }">
      <div class="logo" v-if="!isCollapsed" style="text-align: center">oci-helper</div>

      <el-scrollbar>
        <el-menu
            :default-active="activeMenu"
            @select="handleSelect"
            router
            class="sidebar-menu"
            background-color="transparent"
        >
          <el-menu-item index="home">
            <i class="el-icon-house"></i>
            <span v-if="!isCollapsed">主页</span>
            <span v-if="isCollapsed">home</span>
          </el-menu-item>
          <el-menu-item index="user">
            <i class="el-icon-document"></i>
            <span v-if="!isCollapsed">配置列表</span>
            <span v-if="isCollapsed">config</span>
          </el-menu-item>
          <el-menu-item index="createTask">
            <i class="el-icon-s-management"></i>
            <span v-if="!isCollapsed">开机任务列表</span>
            <span v-if="isCollapsed">task</span>
          </el-menu-item>
          <el-menu-item index="ociLog">
            <i class="el-icon-s-operation"></i>
            <span v-if="!isCollapsed">服务日志</span>
            <span v-if="isCollapsed">log</span>
          </el-menu-item>
        </el-menu>
      </el-scrollbar>
    </el-aside>

    <!-- 主内容区域 -->
    <el-container>
      <el-header class="header">
        <span></span>
        <div class="toggle-button" @click="toggleSidebar">
          <el-icon v-if="isCollapsed">
            <Expand />
          </el-icon>
          <el-icon v-else>
            <Fold />
          </el-icon>
        </div>
        <button @click="showLogoutConfirm" class="logout-button">退出登录</button>
      </el-header>
      <el-main>
        <router-view></router-view> <!-- 显示区域，用于切换内容 -->
      </el-main>
      <el-footer style="text-align: center;">
        <el-link href="https://github.com/Yohann0617" target="_blank">© Yohann All Rights Reserved</el-link>
      </el-footer>
    </el-container>
  </el-container>
</template>

<script>
import { Fold, Expand } from '@element-plus/icons-vue';
export default {
  components: {
    Fold,
    Expand,
  },
  data() {
    return {
      activeMenu: 'home',
      isCollapsed: false // 控制侧边栏状态
    };
  },
  methods: {
    handleSelect(index) {
      this.activeMenu = index;
      this.$router.push(`/dashboard/${index}`);
    },
    toggleSidebar() {
      this.isCollapsed = !this.isCollapsed;
    },
    showLogoutConfirm() {
      const confirmed = window.confirm('确定要退出登录吗?');
      if (confirmed) {
        this.logout();
      } else {
        this.$message.info('取消退出');
      }
    },
    logout() {
      sessionStorage.removeItem('token');
      this.$router.push('/login');
    }
  }
};
</script>

